.form {
  &__input {
    width: 100%;
    height: 48px;
    box-sizing: border-box;
    @include formInput;

    &:hover {
      @include formInputHover;
    }

    &:focus {
      @include formInputFocus;
    }
  }

  &__textarea {
    box-sizing: border-box;
    width: 100%;
    height: 148px;
    resize: none;
    @include formInput;

    &:hover {
      @include formInputHover;
    }

    &:focus {
      @include formInputFocus;
    }
  }

  &__button {
    background-color: #1b2129;
    width: 100%;
    height: 48px;
    border-radius: 8px;
    color: #fff;
    border: none;

    &:hover {
      background-color: #1b2129;
    }

    &:focus {
      background-color: #f98921;
    }
  }
}
